$color-primary: #c69963;
$color-primary-dark: #b28451;
$color-secondary: #101d2c;
.amenities {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 2rem;
  justify-content: space-around;
  width: 100%;
}
.amenities-title {
  color: $color-secondary;
  font-size: 4rem;
  text-align: center;
  width: 100%;
  margin-top: 2rem;
}
.amenities-subtitle {
  color: $color-primary-dark;
  font-size: 2rem;
  text-align: center;
  width: 100%;

  margin-bottom: 4rem;
}
.amenitie {
  width: 47rem;
  color: $color-secondary;
  padding: 2rem;
  display: flex;
  flex-direction: column;

  .title {
    top: 50%;
    font-size: 2rem;
    display: flex;
    justify-content: space-between;
    font-weight: bold;
    text-align: center;

    .icon {
      color: #b28451;
      font-size: 5rem;
      height: 5rem;
      width: 5rem;
    }
  }
  .text {
    font-size: 1.6rem;
    // text-align: justify;
  }
}
@media (max-width: 1024px) {
  .amenities {
    padding: 2rem;
    flex-direction: row;
    justify-content: center;
    align-content: center;
  }
  .amenitie {
    margin: 1rem;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    width: 44%;
  }
  .amenitie-icon {
    color: #b28451;
    font-size: 10rem;
    height: 5rem;
    width: 5rem;
    svg {
      font-size: 5rem;
      height: 5rem;
      width: 5rem;
    }
  }
  .amenitie-text {
    font-size: 2rem;
  }
}
