$color-primary: #c69963;
$color-primary-dark: #b28451;
$color-primary-darker: darken($color-primary, 20);
$color-secondary: #101d2c;
$color-secondary-light: #213f61;
// first page of booking translates to info page
.bookpage {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  padding: 2rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
// calendar and text below it
.bookpage-datepicker {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 40%;
}
.bookpage-datepicker-text {
  text-align: center;
  margin: 1rem;
  font-size: 1.5rem;
  color: $color-secondary;
}
//selects
.bookpage-selects {
  display: flex;
  // justify-content: space-around;
  height: 35rem;
  width: 60%;
}
// room picking part
.bookpage-roomselect {
  display: flex;
  flex-direction: column;
  width: 60%;
}
.bookpage-roomselect-title {
  margin: 1rem;
  font-size: 3rem;
  color: $color-secondary;
}
.bookpage-roomselect-options {
  display: flex;
  flex-direction: column;
  height: 100%;
  align-content: center;
}
.bookpage-room {
  margin: 1rem;
}
.bookpage-room-price {
  font-size: 1.5rem;
}
// cruise options
.bookpage-cruiseselect {
  display: flex;
  flex-direction: column;

  width: 40%;
}
.bookpage-cruise-title {
  margin: 1rem;
  font-size: 3rem;
  color: $color-secondary;
}
.bookpage-cruiseselect-options {
  display: flex;
  flex-direction: column;
  justify-content: space-around;

  align-content: center;
}
.bookpage-cruiseselect-option {
  margin: 1rem;
}
.bookpage-cruiseselect-free {
  // text-align: center;
  font-size: 2rem;
  color: $color-secondary;
}
.bookpage-cruiseselect-special {
  //text-align: center;
  font-size: 2rem;
  color: $color-primary-darker;
  margin: 1rem 0;
}
.next-button {
  right: 3rem;
  bottom: 3rem;
  position: absolute;
  padding: 1rem;
  height: min-content;
  width: 25rem;
  align-self: flex-end;
  font-family: inherit;
  background-color: $color-secondary;
  color: white;
  font-size: 2rem;
  text-align: center;
  outline: none;
  border-radius: 0.5rem;
  border: none;
  cursor: pointer;
  &:hover {
    background-color: lighten($color-secondary, 4);
  }
  &:active {
    transform: translateY(0.2rem);
  }
  &:disabled {
    background-color: #566575;
    cursor: not-allowed;
  }
}
.total {
  left: 3rem;
  bottom: 3rem;
  position: absolute;
  font-size: 2.5rem;
  color: $color-secondary;
}
///////////////////////////////////////////////////////////////////////

@media (max-width: 650px) {
  .bookpage {
    flex-direction: column;
    padding: 1rem;
    justify-content: space-around;
  }
  .bookpage-datepicker {
    margin-top: 4rem;
    width: 100%;
    height: min-content;
  }
  .bookpage-datepicker-text {
    margin: 0.5rem;
  }
  .bookpage-selects {
    flex-direction: column;
    width: 100%;
  }
  .bookpage-roomselect {
    flex-direction: column;
    width: 100%;
  }
  .bookpage-roomselect-title {
    margin: 0.5rem;
    font-size: 2.2rem;
  }
  .bookpage-roomselect-options {
    flex-direction: row;
    width: 100%;
  }
  .bookpage-room {
    margin: 0.5rem;
  }
  .bookpage-cruiseselect {
    width: 100%;
    flex-direction: column;
  }
  .bookpage-cruise-title {
    margin: 0.5rem;
    font-size: 2.2rem;
  }
  .bookpage-cruiseselect-options {
    width: 100%;
    flex-direction: row;
  }
  .bookpage-cruiseselect-free {
    // text-align: center;
    display: flex;
    align-items: center;
    font-size: 1.2rem;
    color: $color-secondary;
  }
  .bookpage-cruiseselect-special {
    display: flex;
    align-items: center;
    font-size: 1.2rem;
    color: $color-primary-darker;
    margin: 0.5rem 0;
  }
}

@media (max-width: 350px) {
  .next-button {
    right: 1.5rem;
    bottom: 1.5rem;
  }
  .total {
    left: 1.5rem;
    bottom: 1.5rem;
  }
}
// css transition animation
.swap-enter {
  transform: translateX(100%);
}
.swap-enter-active {
  transform: translateX(0%);
  transition: transform 900ms ease-in-out;
}

.swap-exit {
  transform: translateX(0%); //0
}
.swap-exit-active {
  transform: translateX(-100%); //100
  transition: transform 900ms ease-in-out;
}
.swap-exit-done {
  display: none;
}
